.recipes {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 540px;
  border-radius: 6px;
  background-color: $black2;
  background-position: center;
  background-size: cover;
  padding: 3rem;
  padding-bottom: 6rem;
  &__btn-back {
    margin-left: 44rem; }
  &__alert {
    margin-bottom: 6.5rem; }
  &__disc {
    width: 36rem;
    font-size: 24px;
    font-weight: 500;
    text-align: center;
    line-height: 1.67;
    color: $white;
    margin-top: 2.5rem; }
  &__name {
    font-size: 1.4rem;
    line-height: 2.14;
    color: $white;
    margin: 4.5rem 0 6rem; }
  &__footer {
    display: flex; }
  &__views {
    color: $white;
    font-size: 2.2rem;
    vertical-align: middle; }
  &__comments {
    color: $white;
    font-size: 2.2rem;
    vertical-align: sub; } }

.recipes-mini {
  text-align: left;
  background-color: $black2;
  color: $white;
  box-shadow: 0 2px 5px 0 $boxShadow;
  border-radius: 0.7rem;
  padding: 3rem 2.5rem 2.5rem 3rem;
  &_photo {
    width: 54rem;
    background-image: url(../img/recipes1.jpg);
    background-position: center;
    background-size:  cover;
    &:hover {
      background-image: none; } }
  &__disc {
    @include lead3(); }
  &__footer {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 1rem; }
  &__views {
    font-size: 2.2rem;
    vertical-align: middle; }
  &__comments {
    font-size: 2.2rem;
    vertical-align: sub; }
  &:hover {
    background-color: $primaryColor;
    .recipes-mini__disc {
      color: $black2; }
    .recipes-mini__views, .recipes-mini__comments, .info-table-counter, .info-table-data {
      color: $black3; }
    .alert {
      background-color: $black; } } }
