html,
body {
  font-family: $mainFont;
  font-size: 10px;
  font-weight: normal;
  background-color: $primaryBackground; }

.container {
  width: 100%;
  max-width: 1110px;
  margin: 0 auto; }

.uik__title {
  color: $primaryColor;
  font-size: 2rem;
  margin-bottom: 1rem;
  margin-top: 2rem;
  text-decoration: underline; }

.title-uik {
  h3, h4, h5 {
    margin-top: 0;
    text-align: center; } }

.divider {
  height: 50px; }


// Titles
.h1 {
  @include h1(); }

.h2 {
  @include h2(); }

.h3 {
  @include h3(); }

.h4 {
  @include h4(); }

.h5 {
  @include h5(); }

.h6 {
  @include h6(); }

.h7 {
  @include h7(); }

// Text
.lead {
  @include lead(); }

.lead2 {
  @include lead2(); }

.lead3 {
  @include lead3(); }

.p1 {
  @include p1(); }

.p2 {
  @include p2(); }

.sub_txt {
  @include sub_txt(); }

.sub_txt2 {
  @include sub_txt2(); }

.sub_txt3 {
  @include subTxt3(); }

// Buttons
.btn {
  @extend %btn; }

.btn-big {
  @extend %btn-big; }

.btn-small {
  @extend %btn-small; }

.btn-circle_big {
  @extend %btn-circle_big; }

.btn-circle_small {
  @extend %btn-circle_small; }

.btn-forFlyer {
  @extend %btn-forFlyer; }

.btn-forInstagram {
  @extend %btn-forInstagram; }

.btn-social {
  @extend %btn-social; }

// Icons
.navSlider {
  @extend %navSlider; }

.moveToCard {
  @extend %moveToCard; }

.navFlyer {
  @extend %navFlyer; }

.navSocial {
  @extend %navSocial; }

// Forms
.input {
  @extend %input; }

.textarea {
  @extend %textarea; }

// Decor
.decor-line {
  @include decorLine(); }

.title-line {
  @include titleTine(); }
