.tutorial {
  background-color: $secondaryBackgroung;
  text-align: left;
  padding: 2.5rem;
  border-radius: 0.6rem;
  box-shadow: $boxShadow;
  &__title {
    @include h6();
    margin-bottom: 2rem; }
  &__disc {
    @include p1();
    margin-top: 2.5rem; }
  &:hover {
    background-color: $primaryColor;
    .tutorial__title {
      color: $black3; }
    .tutorial__disc {
      color: $black3; }
    .decor-line {
      background-color: $black3; } } }
