.social-card {
  width: 35rem;
  border-radius: 6px;
  box-shadow: $boxShadow;
  background-color: $black2;
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 2.5rem;
    border-bottom: 2px solid $secondaryColor; }
  &__icon {
    display: flex;
    align-items: center; }
  &__name {
    font-size: 14px;
    text-transform: capitalize;
    line-height: 2.14;
    color: $gray7;
    margin-left: 2rem; }
  &__btn {
    padding: 0 1.8rem; }
  &__main {
    padding: 2rem 1.5rem 2rem 3rem; }
  &__date {
    font-size: 14px;
    line-height: 2.14;
    color: $gray5; }
  &__disc {
    width: 28rem;
    height: 12rem;
    font-size: 14px;
    line-height: 2.14;
    color: $opacity2;
    overflow-y: auto;
    scroll-behavior: smooth; }
  &__link {
    color: $primaryColor;
    font-size: 14px;
    line-height: 2.14;
    margin-top: 1rem; } }
