.shef {
  &__profile {
    display: flex;
    flex-direction: column;
    align-items: center;
    @media ($tablet) {
      flex-direction: row; } }
  &__avatar {
    @include img();
    border-radius: 50%;
    margin-bottom: 1rem;
    @media ($tablet) {
      margin-bottom: 0; } }
  &__txt {
    padding-left: 0;
    text-align: center;
    @media ($tablet) {
      padding-left: 2.4rem;
      text-align: left; } }
  &__title {
    @include h7(); }
  &__disc {
    @include subTxt3();
    margin-top: 0.5rem; }
  &__dishes {
    display: flex;
    justify-content: center;
    margin-top: 2.4rem;
    @media ($tablet) {
      justify-content: flex-start; } }
  &__img {
    margin-right: 3rem;
    &:last-child {
      margin-right: 0; } } }
