.dish {
  max-width: 350px;
  border-radius: 6px;
  box-shadow: $boxShadow;
  background-color: $secondaryBackgroung;
  &__img {
    @include img();
    width: 100%;
    height: 26rem;
    border-radius: 0.6rem 0.6rem 0 0;
    padding: 2.4rem 0 0 28.6rem; }
  &__txt {
    padding: 3rem 2.4rem 0 3rem; }
  &__title {
    @include h4(); }
  &__subtext {
    @include subTxt3();
    margin-bottom: 2.1rem; }
  &__disc {
    @include p1();
    width: 28rem; }
  &__footer {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2.4rem 0; }
  &__rating {
    &:hover {
      .dish__rating-star {
        color: $gold; } } }
  &__rating-star {
    @include ratingStar();
    margin-right: 0.5rem;
    &:last-child {
      margin-right: 0; } }
  &__counter {
    color: $gray6;
    font-size: 1.2rem;
    margin: 0 4.3rem 0 0.5rem; } }
